import petsJSON from './pets.json';

export class Carousel {
  constructor() {
    this.petsList = petsJSON;
    this.arrowRight = document.getElementById('arrow-right');
    this.arrowLeft = document.getElementById('arrow-left');
    this.slider = document.getElementById('slider-carousel');
    this.sliderContainer = document.getElementById('slider-carousel-container');
    this.countCardsOnPage = this.getCountCardsOnPage();
    this.isRun = false;
  }

  init() {
    this.setCurrentStyleForCardOnload();
    this.updateDocument();
    this.arrowLeft.addEventListener('click', this.moveToLeft.bind(this));
    this.arrowRight.addEventListener('click', this.moveToRight.bind(this));
  }

  //After load page
  getCountCardsOnPage() {
    const widthContainer = window.innerWidth;
    const mediaTwoCardsWidth = 1130;
    const mediaOneCardsWidth = 767;
    if (widthContainer > mediaTwoCardsWidth) return 3;
    else if (widthContainer > mediaOneCardsWidth) return 2;
    else return 1;
  }

  setCurrentStyleForCardOnload() {
    const listOfCards = document.querySelectorAll('.pets-slider__item');
    for (let i = 0; i < this.countCardsOnPage; i++) {
      listOfCards[i].classList.add('pets-slider__item_current');
    }
  }

  // Render new cards

  getRandomPets() {
    const listOfCards = document.querySelectorAll('.pets-slider__item_current');
    const arrNotRepeatingPets = this.petsList.slice();
    const resultArrPets = [];
    listOfCards.forEach((card) => {
      const name = card.querySelector('.pet-card__title').innerText;
      const index = arrNotRepeatingPets.findIndex((pet) => pet.name === name);
      arrNotRepeatingPets.splice(index, 1);
    });

    for (let i = 0; i < this.countCardsOnPage; i++) {
      const randomIndex = Math.floor(Math.random() * this.countCardsOnPage);
      resultArrPets.push(arrNotRepeatingPets.splice(randomIndex, 1)[0]);
    }
    return resultArrPets;
  }

  renderPet(pet, classForElement) {
    const li = document.createElement('li');
    if (classForElement) {
      li.classList.add('pets-slider__item', classForElement);
    } else {
      li.classList.add('pets-slider__item');
    }

    li.innerHTML = `<div class="pet-card">
    <img
      src="assets/images/pets-${pet.name.toLowerCase()}.png"
      alt="Image pet"
    />
    <span class="pet-card__title">${pet.name}</span>
    <div class="button button_per-card">
      <span class="button__title">Learn more</span>
    </div>
  </div>`;
    return li;
  }

  updateDocument() {
    const arrPets = this.getRandomPets();
    let cardsList = document.querySelectorAll('.pets-slider__item');

    if (cardsList.length > this.countCardsOnPage) {
      for (let i = 0; i < cardsList.length; i++) {
        if (i + 1 > this.countCardsOnPage) cardsList[i].remove();
      }
    }

    for (let i = 0; i < this.countCardsOnPage; i++) {
      this.slider.prepend(this.renderPet(arrPets[i], 'pets-slider__item_prev'));
      this.slider.append(this.renderPet(arrPets[i], 'pets-slider__item_next'));
    }
    cardsList = document.querySelectorAll('.pets-slider__item');
    cardsList.forEach((card) => {
      card.style.transition = 'transform 0.8s';
    });
  }

  removeCards() {
    const cardsList = document.querySelectorAll('.pets-slider__item');
    cardsList.forEach((card) => {
      card.style.transition = '';
    });
    cardsList.forEach((card) => {
      const isCurrent = card.classList.contains('pets-slider__item_current');
      if (!isCurrent) {
        card.remove();
      } else {
        card.style.transform = `translateX(0)`;
      }
    });
  }

  updateCurrentCardsStyle(isPrev) {
    const cardsCurrent = document.querySelectorAll(
      '.pets-slider__item_current'
    );
    cardsCurrent.forEach((card) => {
      card.classList.remove('pets-slider__item_current');
    });
    if (isPrev) {
      const cardsPrev = document.querySelectorAll('.pets-slider__item_prev');
      cardsPrev.forEach((card) => {
        card.classList.remove('pets-slider__item_prev');
        card.classList.add('pets-slider__item_current');
      });
    } else {
      const cardsNext = document.querySelectorAll('.pets-slider__item_next');
      cardsNext.forEach((card) => {
        card.classList.remove('pets-slider__item_next');
        card.classList.add('pets-slider__item_current');
      });
    }
  }

  //EventHandlers
  moveToLeft() {
    if (this.isRun) return;
    this.isRun = true;
    const isPrev = true;
    const arrCards = document.querySelectorAll('.pets-slider__item');
    const that = this;
    arrCards.forEach((card) => {
      card.style.transform = `translateX(${this.countCardsOnPage * 100}%)`;
    });
    this.updateCurrentCardsStyle(isPrev);
    setTimeout(function () {
      that.removeCards.bind(that)();
      that.updateDocument.bind(that)();
      that.isRun = false;
    }, 800);
  }

  moveToRight() {
    if (this.isRun) return;
    this.isRun = true;
    const isPrev = false;
    const arrCards = document.querySelectorAll('.pets-slider__item');
    const that = this;
    arrCards.forEach((card) => {
      card.style.transform = `translateX(-${this.countCardsOnPage * 100}%)`;
    });
    this.updateCurrentCardsStyle(isPrev);
    setTimeout(function () {
      that.removeCards.bind(that)();
      that.updateDocument.bind(that)();
      that.isRun = false;
    }, 800);
  }

  getTranslateX(elem) {
    const style = window.getComputedStyle(elem);
    const matrix =
      style.transform || style.webkitTransform || style.mozTransform;
    let x;

    // Can either be 2d or 3d transform
    if (matrix) {
      const matrixType = matrix.includes('3d') ? '3d' : '2d';
      const matrixValues = matrix.match(/matrix.*\((.+)\)/)[1].split(', ');
      if (matrixType === '2d') {
        x = matrixValues[4];
      }
      if (matrixType === '3d') {
        x = matrixValues[12];
      }
    }

    return x;
  }
}
