import petsJSON from './pets.json';

export class Popup {
  constructor() {
    this.isActive = false;
    this.petsList = petsJSON;

    this.popup = document.getElementById('popup');
    this.overlay = document.getElementById('overlay');
  }

  init() {
    document.addEventListener(
      'DOMContentLoaded',
      this.setTransition.bind(this)
    );
    this.setCardListeners();
    this.updateCardListeners();

    this.overlay.addEventListener('click', (e) => {
      if (e.target.closest('#popup') && !e.target.closest('#popup-button'))
        return;
      this.changeState();
    });
  }

  setTransition() {
    this.popup.style.transition = 'transform 0.5s';
    console.log('ffdf');
  }

  updateCardListeners() {
    let isUpdate = false;
    const arrows = document.querySelectorAll('.pets-slider__arrow');
    arrows.forEach((arrow) => {
      arrow.addEventListener(
        'click',
        function () {
          if (!isUpdate) {
            isUpdate = true;
            setTimeout(
              function () {
                const cardsListPrev = document.querySelectorAll(
                  '.pets-slider__item_prev'
                );
                const cardsListNext = document.querySelectorAll(
                  '.pets-slider__item_next'
                );
                cardsListPrev.forEach((card) => {
                  card.addEventListener(
                    'click',
                    this.cardClickHandler.bind(this)
                  );
                });
                cardsListNext.forEach((card) => {
                  card.addEventListener(
                    'click',
                    this.cardClickHandler.bind(this)
                  );
                });
                isUpdate = false;
              }.bind(this),
              800
            );
          }
        }.bind(this)
      );
    });
  }

  setCardListeners() {
    this.cardsList = document.querySelectorAll('.pets-slider__item');
    console.log(this.cardsList);
    this.cardsList.forEach((card) => {
      card.addEventListener('click', this.cardClickHandler.bind(this));
    });
  }

  cardClickHandler(e) {
    const pet = this.getPet(e.currentTarget);

    this.render(pet);
    this.changeState();
  }

  getPet(target) {
    const petName = target.querySelector('.pet-card__title').innerText;
    const pet = this.petsList.find((pet) => pet.name === petName);
    return pet;
  }

  changeState(e) {
    this.isActive = !this.isActive;
    this.popup.classList.toggle('popup_active', this.isActive);
    document.body.classList.toggle('overflow-y', this.isActive);
    this.overlay.classList.toggle('popup-overlay', this.isActive);
  }

  render(pet) {
    for (let prop in pet) {
      const element = document.getElementById(`popup-${prop}`);
      if (element) {
        if (prop === 'img') {
          element.src = pet[prop];
        } else if (
          prop === 'inoculations' ||
          prop === 'diseases' ||
          prop === 'parasites'
        ) {
          element.innerText = pet[prop].join(', ');
        } else if (prop === 'type')
          element.innerText = `${pet[prop]} - ${pet.breed}`;
        else element.innerText = pet[prop];
      }
    }
  }
}
